/* Experience.css */
.experience-section {
  padding: 4em 2em;
  background-color: #f4f4f4;
  position: relative;
}

.experience-section h2 {
  text-align: center;
  font-size: 2.5em;
  margin-bottom: 1em;
  color: #333;
}

.timeline {
  position: relative;
  padding: 2em 0;
  margin-top: 30px;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 4px;
  background: #ffd700;
  transform: translateX(-50%);
}

.timeline-item {
  position: relative;
  width: 50%;
  padding: 1em;
  box-sizing: border-box;
  margin-bottom: 2em;
}

.timeline-item:nth-child(odd) {
  left: 0;
}

.timeline-item:nth-child(even) {
  left: 50%;
}

.timeline-icon {
  width: 50px;
  height: 50px;
  background: #fff;
  border: 4px solid #ffd700;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffd700;
  font-size: 24px;
}

.timeline-content {
  background: #fff;
  padding: 1.5em;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: relative;
  transition: transform 0.3s ease;
}

.timeline-item:hover .timeline-content {
  transform: translateY(-5px);
}

.company {
  font-size: 1.5em;
  margin-bottom: 0.5em;
  color: #004080;
  display: flex;
  align-items: center;
}

.company .fa-building {
  margin-right: 0.5em;
}

.role,
.duration,
.location {
  font-size: 1.1em;
  color: #666;
  display: flex;
  align-items: center;
  margin-bottom: 0.5em;
}

.role .fa-tasks,
.duration .fa-calendar-alt,
.location .fa-calendar-alt {
  margin-right: 0.5em;
}

.responsibilities {
  list-style: disc;
  padding-left: 20px;
  margin-top: 1em;
}

.responsibilities li {
  font-size: 1em;
  margin-bottom: 0.5em;
  color: #333;
}

/* Responsive Design */
@media (max-width: 768px) {
  .timeline {
    padding: 0;
  }

  .timeline::before {
    left: 20px;
  }

  .timeline-item {
    width: 100%;
    padding-left: 50px;
    margin-bottom: 3em;
  }

  .timeline-item:nth-child(odd),
  .timeline-item:nth-child(even) {
    left: 0;
  }

  .timeline-icon {
    left: 20px;
  }

  .timeline-content {
    margin-left: 70px; /* Ensure proper alignment */
  }

  .company {
    font-size: 1.2em; /* Slightly smaller text for better fit */
  }

  .role,
  .duration,
  .location {
    font-size: 1em;
  }

  .responsibilities {
    margin-left: 70px; /* Align with content */
  }
}

@media (max-width: 480px) {
  .timeline-icon {
    width: 40px;
    height: 40px;
    font-size: 20px;
    left: 15px;
  }

  .timeline-content {
    margin-left: 60px;
    padding: 1em; /* Reduce padding for smaller screens */
  }

  .company {
    font-size: 1em;
  }

  .role,
  .duration,
  .location {
    font-size: 0.9em;
  }

  .responsibilities {
    font-size: 0.9em;
  }
}
